
const lineIndicator = document.querySelector('.body-line__indicator');
const backBtn = document.querySelector('.back');
const checkPriceBtn = document.querySelector('.check-price');
const contentFormInnerSuccess = document.querySelector('.content-form__inner-success');
const contentFormInner = document.querySelector('.content-form__inner');

//photos
const phonePhoto = document.querySelector('.phone');
const employeePhoto = document.querySelector('.employee');
//First screen
const content = document.querySelector('.content');
const contentItem = document.querySelectorAll('.content__list-item');
const contentForm = document.querySelector('.content-form');
const autoName = document.querySelector('.mark-name');
const checkPriceTwoBtn = document.querySelector('.check-cost');
const thanksPage = document.querySelector('.content-form__thanks-page');
const progressLine = document.querySelector('.body-line');


contentForm.addEventListener('submit', function (event) {
  event.preventDefault();
})

function hideContent(event) {
  autoName.textContent = event.target.alt;
  contentForm.style.display = 'block';
  content.style.display = 'none';
  thanksPage.style.display = 'none';
  contentFormInnerSuccess.style.display = 'none';
  lineIndicator.style.width = '66.666%';
}

contentItem.forEach(function (item) {
  item.addEventListener('click', hideContent);
});

//Second screen
function goBack() {
  contentForm.style.display = 'none';
  content.style.display = 'block';
  lineIndicator.style.width = '33.33%';
}

backBtn.addEventListener('click', goBack);

function getPrice() {
  employeePhoto.style.opacity = '0';
  content.style.display = 'none';
  progressLine.style.display = 'none';
  contentFormInner.style.display = 'none';
  contentFormInnerSuccess.style.display = 'block';
  phonePhoto.style.opacity = '1';
}

checkPriceBtn.addEventListener('click', getPrice);

//Third screen
const goBackTwoBnt = document.querySelector('.go-back');

function goBackTwo() {
  contentFormInner.style.display = 'block';
  progressLine.style.display = 'block';
  contentFormInnerSuccess.style.display = 'none';
  phonePhoto.style.opacity = '0';
  employeePhoto.style.opacity = '1';
}

goBackTwoBnt.addEventListener('click', goBackTwo);

function checkPriceTwo() {
  contentFormInnerSuccess.style.display = 'none';
  thanksPage.style.display = 'block';
}

checkPriceTwoBtn.addEventListener('click', checkPriceTwo);
